
import 'regenerator-runtime/runtime'; // for async/await transpile
import '../styles/main.css';
import './components/restaurant-footer.js';
import './components/restaurant-navbar.js';

document.addEventListener('DOMContentLoaded', async () => {
  try {
  
    const jsonData = await import('../DATA.json');

    const restaurantData = jsonData.default.restaurants;

    let restaurantCardsHTML = '';

    restaurantData.forEach((restaurant) => {
      restaurantCardsHTML += `
        <div class="card">
          <img src="${restaurant.pictureId}" alt="${restaurant.name}">
          <h3><a href="#">${restaurant.name}</a></h3>
          <p>${restaurant.city}</p>
          <p>${restaurant.rating}</p>
          <p>${restaurant.description.slice(0, 150)}...</p>
        </div>
      `;
    });

    document.querySelector('#restaurant-cards').innerHTML = restaurantCardsHTML;
  } catch (error) {
    console.error(error);
  }
});





