class NavbarRestaurant extends HTMLElement {
  connectedCallback() {
      this.render();
  }

  render() {
      this.innerHTML = `
      <nav>
    <div class="app-logo">
      <a href="#" tabindex="1">
        <img src="images/favicon.ico" alt="Restoran App Logo">
        <!-- <h1>Restoran App</h1> -->
      </a>
    </div>
    <ul class="main-menu">
      <li><a href="#" tabindex="2">Home</a></li>
      <li><a href="#" tabindex="3">Favorites</a></li>
      <li><a href="https://www.linkedin.com/in/samidin-samidin-46386b264/" tabindex="4">About Us</a></li>
    </ul>

    <button class="menu-toggle" aria-label="Toggle navigation" aria-controls="main-menu" tabindex="5">
    <input type="checkbox" />
      <span></span>
      <span></span>
      <span></span>
    </button>
  </nav>
      `;
      const toggleButton = document.querySelector('.menu-toggle input');
const nav = document.querySelector('nav ul');

toggleButton.addEventListener('click', function() {
if (this.checked) {
  nav.classList.add('slide');
} else {
  nav.classList.remove('slide');
}
});
  }

}

customElements.define("restaurant-navbar", NavbarRestaurant);

